.pl__container {
  display: block;
  position: relative;
  overflow: hidden;
  width:  100%;
  height: 5px;
  -webkit-transform: translate(0, 0) scale(1, 1);
          transform: translate(0, 0) scale(1, 1);
}

.pl__container > pl__bar {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 5px;
}

.pl__container > pl__dashed::before {
  content: "";
  display: none;
  position: absolute;
  margin-top: 0;
  height: 5px;
  width: 100%;
  background-color: transparent;
  background-size: 10px 10px;
  background-position: 0px -23px;
}

.pl__container > pl__bar__1, .pl__container > pl__bar__2 {
  -webkit-transition: -webkit-transform 0.2s linear;
  transition: -webkit-transform 0.2s linear;
  transition: transform 0.2s linear;
  transition: transform 0.2s linear, -webkit-transform 0.2s linear;
}

.pl__container.pl__mode__query > pl__bar__1, .pl__container.pl__mode__determinate > pl__bar__1 {
  display: none;
}

.pl__container.pl__mode__query > pl__bar__2 {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  -webkit-animation: query 0.8s infinite cubic-bezier(0.390, 0.575, 0.565, 1.000);
          animation: query 0.8s infinite cubic-bezier(0.390, 0.575, 0.565, 1.000);
}

.pl__container.indeterminate  > pl__bar__1, .pl__container.indeterminate  > pl__bar__2 {
  display: none;
}

@-webkit-keyframes query {
  0% {
    opacity: 1;
    -webkit-transform: translateX(35%) scale(0.3, 1);
            transform: translateX(35%) scale(0.3, 1);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateX(-50%) scale(0, 1);
            transform: translateX(-50%) scale(0, 1);
  }
}

@keyframes query {
  0% {
    opacity: 1;
    -webkit-transform: translateX(35%) scale(0.3, 1);
            transform: translateX(35%) scale(0.3, 1);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateX(-50%) scale(0, 1);
            transform: translateX(-50%) scale(0, 1);
  }
}

@-webkit-keyframes buffer {
  0% {
    opacity: 1;
    background-position: 0px -23px;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    background-position: -200px -23px;
  }
}

@keyframes buffer {
  0% {
    opacity: 1;
    background-position: 0px -23px;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    background-position: -200px -23px;
  }
}

